import React, { useMemo } from 'react'
import styled from '@emotion/styled'
import Tab from './Tab'
import BlogName from './BlogName'
import SearchBar from './SearchBar'
// import { PostListItemType } from 'types/PostItem.types'
import { useStaticQuery, graphql } from 'gatsby'

const HeaderWrapper = styled.header`
  // Header 
  display: block;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;

  //  
  width: 100%;
  height: 60px;
  left: 50%;
  z-index: 300;
  position: fixed;
  transform: translateX(-50%);
  backdrop-filter: blur(3px);
  background-color: rgba(255, 255, 255, 0.85);

  @media (min-width: 1280px) {
    width: 1280px;
  }

  @media (min-width: 600px) {
    padding-left: 24px;
    padding-right: 24px;
  }
`

const HeaderContentWrapper = styled.div`
  position: relative;
`

const ItemWrapper = styled.div`
  align-items: center;
  justify-content: space-between;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
`

const MainHeader = () => {
  const data = useStaticQuery(graphql`
    query SearchIndexQuery {
      allMarkdownRemark(
        sort: [
          { frontmatter: { date: DESC } }
          { frontmatter: { title: DESC } }
        ]
      ) {
        edges {
          node {
            frontmatter {
              title
              category
            }
            fields {
              slug
            }
          }
        }
      }
    }
  `)

  const searchList = data.allMarkdownRemark.edges.map((value: any) => {
    return {
      title: value.node.frontmatter.title,
      category: value.node.frontmatter.category,
      slug: value.node.fields.slug,
    }
  })

  return (
    <HeaderWrapper>
      <HeaderContentWrapper>
        <ItemWrapper>
          <Tab />
          <BlogName />
          <SearchBar posts={searchList} />
        </ItemWrapper>
      </HeaderContentWrapper>
    </HeaderWrapper>
  )
}

export default MainHeader
