import React, { FunctionComponent, useMemo } from 'react'
import { navigate } from 'gatsby'
import styled from '@emotion/styled'
import SearchIcon from '@mui/icons-material/SearchOutlined'
import { Autocomplete, TextField } from '@mui/material'
import './css/searchbar.css'

// TODO:    

interface SearchBarProps {
  posts: postContent[]
}

interface postContent {
  title: string
  slug: string
  category: string
}

const SearchBarWrapper = styled.div`
  margin: 0;
  box-sizing: border-box;
  @media (min-width: 1280px) {
    flex-grow: 0;
    max-width: 16.666667%;
    flex-basis: 16.666667%;
  }
`

const AutoCompleteWrapper = styled.div`
  @media (max-width: 1080px) {
    display: none;
  }
`

const SearchIconView = styled(SearchIcon)`
  display: none;
  @media (max-width: 1080px) {
    display: block;
    margin-right: 10px;
  }
`

const SearchBar: FunctionComponent<SearchBarProps> = function (posts) {
  const postArray = posts.posts

  return (
    <SearchBarWrapper>
      <AutoCompleteWrapper>
        <Autocomplete
          disableClearable
          options={postArray}
          onInputChange={(event, value, reason) => {
            if (reason === 'reset' && value) {
              const item = postArray.find(item => item.title === value)
              if (!item) return
              navigate(item.slug)
            }
          }}
          filterOptions={(options, { inputValue }) =>
            options.filter(
              ({ title, category }) =>
                title.includes(inputValue) || category == inputValue,
            )
          }
          getOptionLabel={option => option.title}
          renderInput={params => (
            <div className="search-input-wrapper">
              <TextField
                {...params}
                className="search-input"
                variant="standard"
                size="medium"
                InputProps={{
                  ...params.InputProps,
                  endAdornment: <SearchIcon className="search-icon" />,
                }}
              />
            </div>
          )}
          noOptionsText="  ."
        />
      </AutoCompleteWrapper>
      <SearchIconView className="search-icon-view" />
    </SearchBarWrapper>
  )
}

export default SearchBar
